.About {
  width: 100vh;
  height: 80vh;

  justify-self: end;
  margin-right : 10vh;

  z-index: 10;
  margin-top: 10vh;

  @media screen and (orientation: portrait) {
    margin: 0;
    width: 100vw;
    height: 100vh;
  }

  transform: translateX(100vw);
  opacity: 0;

  &__content {
    width: 100vh;
    height: 100%;
    position: fixed;
    z-index: 1000;

    @media screen and (orientation: portrait) {
      margin: 0;
      width: 100vw;
      height: 100vh;
    }

    &__title {
      font-size: 5vh;
      padding-top: 4vh;
      padding-left: 4vh;
      padding-bottom: 10vh;
    }

    &__info {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100vh;
      margin-left: 4vh;
      height: auto;

      @media screen and (max-width: 1366px) {
        width: 95vw;
      }

      @media screen and (min-width: 1367px) and (max-width: 1500px) {
        width: 65vw;
      }

      @media screen and (orientation: portrait) {
        position: fixed;
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        align-items: center;
        width: 100vw;
        height: 80vh;
        margin: 0;
      }

      &__art {
        width : 75vh;
        height : auto;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-content: flex-start;

        @media screen and (max-width: 1366px) {
          position: fixed;
          transform: translateX(25vh);
        }

        @media screen and (min-width: 1367px) and (max-width: 1900px) {
          position: fixed;
          transform: translateX(25vh);
        }

        @media screen and (orientation: portrait) {
          width : 100vw;
          padding-left : 3vh;
          padding-right : 3vh;
          transform: translateX(0vh) translateY(-5vh);
        }

        &__text {
          width: 100%;
          max-width: 70%;
          font-size: 2.7vh;
          height: auto;

          @media screen and (max-width: 1365px) {
            font-size: 2.6vh;
          }

          @media screen and (max-width: 1366px) and (min-width: 1280px) {
            font-size: 2.5vh;
          }

          @media screen and (min-width: 1367px) and (max-width: 1500px) {
            font-size: 2.63vh;
          }

          @media screen and (min-width : 1501px) and (max-width : 1800px) {
            font-size : 2.6vh;
          }

          @media screen and (min-width : 1801px) {
            font-size : 2.6vh;
          }

          @media screen and (orientation: portrait) {
            font-size: 1.9vh;
            max-width: 100%;
            width: 100%;
            height: auto;
            text-align: center;
          }
        }

        &__Buttons {
          width: 45vh;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 1.5vh;

          @media screen and (orientation: portrait) {
            width: 100%;
            margin-top: 4vh;
          }

          &__btn {
            height: 3vh;
            width: 20vh;
            color: white;
            background-color: rgb(153, 1, 153);
            cursor: pointer;
            border: none;
            border-radius: 2vh;
            font-size: 2vh;

            @media screen and (orientation: portrait) {
              height: 4vh;
              width: 32vw;
            }
          }
        }
      }

      &__img {
        width: 22vh;
        height: 30vh;
        border: none;
        position: relative;

        .avatar {
          width: 22.4vh;
          height: 30vh;
        }

        @media screen and (orientation: portrait) {
          width: 13vh;
          height: 19vh;
          transform: translateY(-10vh);

          .avatar {
            width: 15vh;
            height: 20vh;
          }
        }
      }

      &__img::after {
        content: "";
        width: 7vh;
        height: 7vh;
        position: absolute;
        right: 0;
        bottom: 0;
        border-right: solid rgb(153, 1, 153) 0.8vh;
        border-bottom: solid rgb(153, 1, 153) 0.8vh;
      }

      &__img::before {
        content: "";
        width: 7vh;
        height: 7vh;
        position: absolute;
        top: 0;
        left: 0;
        border-left: solid rgb(153, 1, 153) 0.8vh;
        border-top: solid rgb(153, 1, 153) 0.8vh;
      }
    }
  }

  &__waves {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;

    &__wave {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 40%;
      background: url("../wave.png");
      background-size: 300% 100%;
    }

    .wave1 {
      animation: animate1 linear infinite 30s;
      bottom: 0;
    }

    .wave2 {
      animation: animate2 linear infinite 20s;
      opacity: 0.5;
      animation-delay: -1s;
      height: 45%;
    }

    .wave3 {
      animation: animate1 linear infinite 30s;
      opacity: 0.2;
      animation-delay: -3s;
      height: 50%;
    }

    .wave4 {
      animation: animate2 linear infinite 15s;
      opacity: 0.7;
      animation-delay: -6s;
      height: 48%;
    }

    @keyframes animate1 {
      0% {
        background-position-x: 0%;
      }

      50% {
        background-position-x: 100%;
      }

      100% {
        background-position-x: 0%;
      }
    }

    @keyframes animate2 {
      0% {
        background-position-x: 0%;
      }

      50% {
        background-position-x: -100%;
      }

      100% {
        background-position: 0%;
      }
    }
  }
}
